@mixin whiteOnDark {
	font-weight: 100;
	font-family: 'Anton', sans-serif;
	text-transform: uppercase;
	letter-spacing: 2px;
	color: #fff;
	background-color: $dark-blue;
}

@mixin placeholder {
color: $form-gray;
text-transform: uppercase;
font-size: 15px;
font-family: 'Raleway', sans-serif;

}

@mixin dark {
	font-weight: 100;
	font-family: 'Anton', sans-serif;
	text-transform: uppercase;
	letter-spacing: 2px;
	color: $dark-blue;
}
